@import url('https://fonts.googleapis.com/css?family=Droid+Sans+Mono');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

#app {
  display: flex;
  position: relative;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #FBAB7E;
  background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
}

header {
  display: flex;
  position: relative;
}

header h1 {
  font-family: 'Droid Sans Mono', monospace;
  font-weight: lighter;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: white;
}

.flipClock {
  display: flex;
  justify-content: space-between;
  width: calc(3 * 80px + 80px);
}

.flipUnitContainer {
  display: block;
  position: relative;
  width: 100px;
  height: 90px;
  perspective-origin: 50% 50%;
  perspective: 300px;
  background-color: white;
  border-radius: 3px;
  box-shadow: 0px 10px 10px -10px grey;
}

.upperCard, .lowerCard {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  height: 50%;
  overflow: hidden;
  border: 1px solid whitesmoke;
}

.upperCard span, .lowerCard span {
  font-size: 3em;
  font-family: 'Droid Sans Mono', monospace;
  font-weight: lighter;
  color: rgba(0, 0, 0, 0.8);
}

.upperCard {
  align-items: flex-end;
  border-bottom: 0.5px solid whitesmoke;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.upperCard span {
  transform: translateY(50%);
}

.lowerCard {
  align-items: flex-start;
  border-top: 0.5px solid whitesmoke;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.lowerCard span {
  transform: translateY(-50%);
}

.flipCard {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  width: 100%;
  height: 50%;
  overflow: hidden;
  backface-visibility: hidden;
}

.flipCard span {
  font-family: 'Droid Sans Mono', monospace;
  font-size: 3em;
  font-weight: lighter;
  color: rgba(0, 0, 0, 0.8);
}

.flipCard.unfold {
  top: 50%;
  align-items: flex-start;
  transform-origin: 50% 0%;
  transform: rotateX(180deg); /* from 180 to 0 */
  background-color: white;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  border: 0.5px solid whitesmoke;
  border-top: 0.5px solid whitesmoke;
  animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
}

.flipCard.unfold span {
  transform: translateY(-50%);
}

.flipCard.fold {
  top: 0%;
  align-items: flex-end;
  transform-origin: 50% 100%;
  transform: rotateX(0deg); /* from 0 to -180 */
  background-color: white;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  border: 0.5px solid whitesmoke;
  border-bottom: 0.5px solid whitesmoke;
  animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
}

.flipCard.fold span {
  transform: translateY(50%);
}

@keyframes fold {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(-180deg);
  }
}

@keyframes unfold {
  0% {
    transform: rotateX(180deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}


@media (max-width: 768px) {
  .flipClock {
    width: calc(3 * 60px + 60px); /* 전체 시계의 너비를 줄임 */
  }

  .flipUnitContainer {
    width: 70px; /* 각 단위 컨테이너의 너비 조정 */
    height: 70px; /* 각 단위 컨테이너의 높이 조정 */
  }

  .upperCard span, .lowerCard span, .flipCard span {
    font-size: 2.5em; /* 숫자의 폰트 크기 조정 */
  }
}

@media (max-width: 480px) {
  .flipClock {
    flex-direction: column; /* 플립 클록의 방향을 세로로 변경 */
    width: calc(3 * 20px + 30px); /* 더 작은 화면에 맞게 전체 시계의 너비를 더 줄임 */
  }

  .flipUnitContainer {
    width: 20px; /* 각 단위 컨테이너의 너비를 더 줄임 */
    height: 20px; /* 각 단위 컨테이너의 높이를 더 줄임 */
  }

  .upperCard span, .lowerCard span, .flipCard span {
    font-size: 1em; /* 숫자의 폰트 크기를 더 줄임 */
  }
}